import { Component, Input, Output, EventEmitter } from '@angular/core';
import { MessageService } from "app/pages/tzb/customCenter/project-main-manage/project-main-manage-add/project-protocol-query/subject-adjustment/templates/MessageService";
import * as $ from 'jquery';

@Component({
    selector: 'adjust-input',
    template: `<div class="ui-g-6">
                <div class="ui-g-5" style="text-align: right;">
                <span *ngIf="type!=1&&(flag=='1'||flag=='2')"  style="color:red!important;">*</span>
                {{title}}:</div>
                <div class="ui-g-7"><input min= "0" [id]="id"  pInputText [(ngModel)]="val" (keyup)="keyupHandle()" (blur)="callServe()" type="text" [disabled]="type==1 || isDisabled"></div>
              </div>`
})
export class AdjustInputComponent {
    @Input() inputVal: any;
    @Output() output = new EventEmitter();
    id:string='';// by huj++
    val: string = '';
    title: string = '';
    type: number = 0;
    key: string = '';
    isNeedCallServe: boolean = false;
    isDisabled: boolean = false
    // callType: string = '';
    flag:string;
    initCommand: any;// by huj ++
    constructor(
        public msgService: MessageService
    ) {
        this.msgService.change.subscribe((msg: any) => {
            if (msg.type == this.type && msg.key == this.key) {
                this.val = msg.val;
                console.log("this.val"+this.val);
                console.log("this.id"+this.id);
                if (msg.hasOwnProperty('isDisabled')) {
                    this.isDisabled = msg.isDisabled;
                } else {
                    this.isDisabled = true;
                }
                // if (msg.attribute) {// by huj ++
                //     this.domHandler(msg);
                // }else{
                //     if (msg.hasOwnProperty('isDisabled')) {
                //         this.isDisabled = msg.isDisabled;
                //     } else {
                //         this.isDisabled = true;
                //     }
                // }
            }
        })
    }
    // by huj ++
    // domHandler (msg: any) {
    //     if (msg.attribute) {
    //         let t = msg.attribute.split(':');
    //         if (t.length) {
    //             let name = t[0];
    //             let value = t[1];
    //             switch (name) {
    //                 case 'Disabled':
    //                     if(this.isDisabled==true){
    //                         this.isDisabled=false;
    //                         this.flag="1";
    //                     }else{
    //                         this.isDisabled=true;
    //                         this.flag="";
    //                     }
    //                 break;
    //             }
    //         }
    //     }
    // }

    // by huj++
    executeCommand () {
        if (this.initCommand) {
            this.isDisabled = true;
            this.flag="";
        }
    }

    ngOnInit() {
        this.getValues();
        this.InitMiddleAccount1();// by huj++
    }

    // by huj++
    InitMiddleAccount1(){
        debugger;
        let eleval=this.val;
        let eleId=this.id;
        if(eleId=='014_middleAccountNumber_after_4' && eleval==''){
            this.flag="";
            this.isDisabled=true;
        }
    }

    getValues() {
        // debugger;
        // console.log(this.inputVal);
        if (Object.keys(this.inputVal)) {
            for (let property in this.inputVal) {
                if (this.inputVal.hasOwnProperty(property)) {
                    this[property] = this.inputVal[property];
                }
            }
        }
    }

    keyupHandle() {
        let output = {
            val: this.val,
            key: this.key,
            type: this.type,
        }
        this.output.next(output);
    }


    callServe() {
        debugger;
        if (this.isNeedCallServe) {
            let output = {
                val: this.val,
                desc: '',
                key: this.key,
                type: this.type,
                isCallServe: true,
            }
            this.output.next(output);
        }
    }
}