import { Component, Input, Output, EventEmitter } from '@angular/core';
import { MessageService } from "app/pages/tzb/customCenter/project-main-manage/project-main-manage-add/project-protocol-query/subject-adjustment/templates/MessageService";
import { SelectHelper } from "app/pages/tzb/custom/overdue/common/selectHelper";
import * as $ from 'jquery';

@Component({
    selector: 'adjust-dropdown',
    template: `<div class="ui-g-6">
                <div class="ui-g-5" style="text-align: right;">
                <span *ngIf="type!=1&&(flag=='1'||flag=='2')"  style="color:red!important;">*</span>
                {{title}}:</div>
                <div class="ui-g-7"><p-dropdown [options]="options" [id]="id"   [(ngModel)]="val" (onChange)="change()" [disabled]="type==1 || isDisabled"></p-dropdown></div>
              </div>`
})
export class AdjustDropdownComponent {
    @Input() inputVal;
    @Output() output = new EventEmitter();

    selectHelper: SelectHelper = new SelectHelper();

    options: any = [];
    val: string = '';
    id: string='';// by huj++
    desc: string = '';
    title: string = '';
    key: string = '';
    type: number = 0;
    isNeedCallServe: boolean = false;
    isDisabled: boolean = false
    flag: string;
    constructor (
        public msgService: MessageService
    ) {
        this.msgService.change.subscribe((msg: any) => {
            if (msg.type == this.type && msg.key == this.key) {
                this.val = msg.val;
            }
        })
    }

    ngOnInit () {
        this.getValues();
    }

    getValues () {
        if (Object.keys(this.inputVal)) {
            for (let property in this.inputVal) {
                if (this.inputVal.hasOwnProperty(property)) {
                    this[property] = this.inputVal[property];
                }
            }
        }
    }

    change () {
        let output = {
            val: this.val,
            desc: { 
                key: this.key, 
                desc: this.selectHelper.getLabel(this.options, this.val) 
            },
            key: this.key,
            type: this.type
        }
        this.output.next(output);
       
 
        // by huj++       
        this.InitMiddleAccount();
       
        if (this.type == 2) {
              this.callServe();
         }
    }

    // by huj++
    InitMiddleAccount(){
        debugger;
        let eleval=this.val;
        let eleId=this.id;
        if(eleId=='014_havemiddleAccount_after_3'){
            $('#014_middleAccountNumber_after_4').removeAttr("disabled");
            $('#014_payPeriod_after_2').removeAttr("disabled");
            if(eleval=='0'){
                $('#014_middleAccountNumber_after_4').attr("disabled",'true');
                $('#014_payPeriod_after_2').attr("disabled",'true');
                $('#014_middleAccountNumber_after_4').val('');
                $('#014_payPeriod_after_2').val('');
            }
        }
       // this.isNeedCallServe=true;
    }

    callServe () {
        if (this.isNeedCallServe) {
            let output = {
                val: this.val,
                desc: '',
                key: this.key,
                type: this.type,
                isCallServe: true
            }
            this.output.next(output);
        }
    }
    
}