import { HttpService } from 'app/@uisftech/common/service/http.service';
import { TZB_HTTP_COM1 } from "app/@uisftech/common/service/http.constant";
import { API } from "app/pages/tzb/customCenter/project-main-manage/http/project-main-manage.constant";
import { CommonlyMessage } from "app/pages/tzb/custom/overdue/common/msg";
import { MessageService } from './MessageService';


export class CallServe {

    serverInterfaceName: string = '';
    frontElementCheckServeString: string = '';
    resource: any;   // 外部输入的数据
    parameterNames: any = []; // 所有需要的参数名

    commonlyMessage: CommonlyMessage;

    failKey: string = '';


    constructor(
        public httpService: HttpService,
        public messageService: MessageService
    ) {

    }



    handleCheck() {
        debugger;
        let checkItems = this.frontElementCheckServeString.split(',');
        console.log(checkItems);
        let type = checkItems[0];
        let ret: any;

        switch (type) {
            case '0':
                this.serverInterfaceName = checkItems[1];
                this.asyncCall(checkItems[2]);
                break;
            case '1':
                break;
            case '2':
                break;
            case '3':
                ret = this.syncCall();
                break;
            case '4': // by huj ++
                ret = this.attributeCall();
                break;
        }
        return ret;
    }

    // 请求服务
    asyncCall(param: any) {
        debugger
        let resource = this.resource;
        param = this.handleParams(param, resource);
        this.frontElementCheckServe(param).subscribe(data => {
            if (data['returnCode']['code'] == 'success') {
                this.commonlyMessage.logicSuccess(data.returnCode.message)
            } else {
                let fail = this.getFailureResult();
                this.messageService.change.emit(fail);
                this.commonlyMessage.logicError(data.returnCode.message)
            }
        })
    }

    // getKey (source: string) {
    //     // marginAccount-acctNo^custId
    //     let ret = '';
    //     if (source) {
    //         ret = source.split('-')[0];
    //     }
    //     return ret;
    // }

    getFailureResult() {
        let ret = {
            val: '',
            key: this.failKey,
            type: 2,
            isDisabled: false
        };
        return ret;
    }

    handleParams(source: any, data: any) {
        let ret = {};

        source = source.split('^');

        if (source && source.length) {
            source.forEach(element => {
                let names = element.split('-');
                let frontName = names[0];
                let backName = names[1] || names[0];
                ret[backName] = data[frontName];
            });
        }

        return ret;
    }

    // 公共服务
    frontElementCheckServe(params) {
        params = this.httpService.handleParams(params)
        return this.httpService.post(TZB_HTTP_COM1 + this.serverInterfaceName, params);
    }


    // 改变其他元素
    syncCall() {
        // "3,
        // a['projectLimit']*a['marginScale']*0.01,
        // margin"
        debugger
        let items = this.frontElementCheckServeString.split(',');
        let kk = this.frontElementCheckServeString.split(',');
        items.shift();
        let a = this.resource;
        let ret = {
            val: 0,
            key: items[1],
            type: 2
        };

        try {
            if (kk[0] == '3') {
                ret.val = eval(items[0]).toFixed(6);
            } else {
                ret.val = eval(items[0]);
            }
        } catch (ex) {
            // throw ex;
            console.error(ex.message);
        }
        return ret;
    }

    // 自己拼接的计算公式，修改其它动态模块的dom属性
    // by huj ++
    attributeCall() {
        // "4,
        // Disabled: true,
        // margin"
        let items = this.frontElementCheckServeString.split(',');
        items.shift();

        let ret = {
            val: null,
            attribute: items[0],
            key: items[1],
            type: 2
        };

        return ret;
    }



    loadBasicData(param) {
        //this.serverInterfaceName = param.serverInterfaceName;
        this.frontElementCheckServeString = param.frontElementCheckServeString;
        this.resource = param.resource;
        this.commonlyMessage = param.commonlyMessage;
        this.failKey = param.key;
    }


}