import { Component, Input, Output, EventEmitter } from '@angular/core';
import { CALENDAR_CN } from "app/@uisftech/common/constant/config";
import { MessageService } from "app/pages/tzb/customCenter/project-main-manage/project-main-manage-add/project-protocol-query/subject-adjustment/templates/MessageService";
import { DateHelper } from '../../../../../../custom/overdue/common/objecthelper';

@Component({
    selector: 'adjust-calendar',
    template: `<div class="ui-g-6">
                <div class="ui-g-5" style="text-align: right;">
                <span *ngIf="type!=1&&(flag=='1'||flag=='2')"  style="color:red!important;">*</span>
                {{title}}:</div>
                <div class="ui-g-7"><p-calendar [showIcon]="true" [(ngModel)]="val" [monthNavigator]="true" [yearNavigator]="true" yearRange="1917:2117" dateFormat="yy-mm-dd" [locale]="ch" [disabled]="type==1 || isDisabled" (onSelect)="keyupHandle()"></p-calendar></div>
              </div>`
})
export class AdjustCalendarComponent {
    @Input() inputVal: any;
    @Output() output = new EventEmitter();

    dateHelper: DateHelper = new DateHelper();

    ch: any = CALENDAR_CN;
    val: Date = new Date();
    title: string = '';
    type: number = 0;
    key: string = '';
    isNeedCallServe: boolean = false;
    isDisabled: boolean = false
    flag:string;
    constructor(
        public msgService: MessageService
    ) {
        this.msgService.change.subscribe((msg: any) => {
            if (msg.type == this.type && msg.key == this.key) {
                this.val = msg.val;
            }
        })
    }

    ngOnInit() {
        this.getValues();
    }

    getValues() {
        if (Object.keys(this.inputVal)) {
            for (let property in this.inputVal) {
                if (this.inputVal.hasOwnProperty(property)) {
                    this[property] = this.inputVal[property];
                }
            }
        }
    }

    keyupHandle() {
        let output = {
            val: this.dateHelper.format(this.val).split(' ')[0],
            desc: '',
            key: this.key,
            type: this.type
        }
        this.output.next(output);
    }

    callServe() {
        if (this.isNeedCallServe) {
            let output = {
                val: this.val,
                desc: '',
                key: this.key,
                type: this.type,
                isCallServe: true
            }
            this.output.next(output);
        }
    }

}