import { Component, Input, Output, EventEmitter } from "@angular/core";
import { MessageService } from "app/pages/tzb/customCenter/project-main-manage/project-main-manage-add/project-protocol-query/subject-adjustment/templates/MessageService";

@Component({
    selector: 'adjust-product',
    template: `<div class="ui-g-6">
                    <div class="ui-g-4" style="padding-left:0;padding-right:0;text-align:right;">产品编号:</div>
                    <div class="ui-g-8">
                        <div class="ui-g-11" style="padding:0;"><input type="text" pInputText disabled [(ngModel)]="val"/></div>
                        <div class="ui-g-1" style="padding:0;">
                            <div *ngIf="type==2 && !isDisabled" (click)="clickHandle()" style="height: 23px;background-color: #fbfbf7;line-height: 16px;text-align: center;border-radius: 0 3px 3px 0;border: 1px solid #bdbdbd;">
                                <a class="a-hand">
                                    <span>...</span>
                                </a>
                            </div>
                        </div>
                    </div>
              </div>`
})
export class AdjustProductComponent {

    @Input() inputVal: any;
    @Output() output = new EventEmitter();

    val: string = '';
    title: string = '';
    type: number = 0;
    key: string = '';
    isNeedCallServe: boolean = false;
    isDisabled: boolean = false

    constructor (
        public msgService: MessageService
    ) {
        this.msgService.change.subscribe((msg: any) => {
            if (msg.type == this.type && msg.key == this.key) {
                this.val = msg.val;
            }
        })
    }

    ngOnInit() {
        this.getValues();
    }


    getValues() {
        if (Object.keys(this.inputVal)) {
            for (let property in this.inputVal) {
                if (this.inputVal.hasOwnProperty(property)) {
                    this[property] = this.inputVal[property];
                }
            }
        }
    }

    clickHandle() {
        let output = {
            flag: 'chooseProduct',
            val: '',
            key: this.key,
            type: this.type
        }
        this.output.next(output);
    }


    callServe() {
        if (this.isNeedCallServe) {
            let output = {
                val: this.val,
                desc: '',
                key: this.key,
                type: this.type,
                isCallServe: true
            }
            this.output.next(output);
        }
    }

}