import { Component, Input, Output, EventEmitter } from '@angular/core';
import { MessageService } from "app/pages/tzb/customCenter/project-main-manage/project-main-manage-add/project-protocol-query/subject-adjustment/templates/MessageService";
import * as $ from 'jquery';

@Component({
    selector: 'adjust-number',
    template: `<div class="ui-g-6">
                <div class="ui-g-5" style="text-align: right;">
                <span *ngIf="type!=1&&(flag=='1'||flag=='2')" style="color:red!important;">*</span>
                {{title}}:</div>
                <div class="ui-g-7"><input pInputText min="0" [id]="id"  [(ngModel)]="val" (keyup)="keyupHandle()" (blur)="callServe()" type="number" [disabled]="type == 1 || isDisabled"></div>
              </div>`
})
export class AdjustNumberComponent {
    @Input() inputVal: any;
    @Output() output = new EventEmitter();
    id:string='';// by huj++
    val: number = 0;
    title: string = '';
    key: string = '';
    type: number = 0;
    isNeedCallServe: boolean = false;
    isDisabled: boolean = false
    flag:string;
    constructor(
        public msgService: MessageService
    ) {
        this.msgService.change.subscribe((msg: any) => {
            if (msg.type == this.type && msg.key == this.key) {
                this.val = msg.val;
                this.isDisabled = true;
            }
        })
    }

    ngOnInit() {
        this.getValues();
        this.InitpayPeriod();// by huj++
    }


      // by huj++
      InitpayPeriod(){
        debugger;
        let eleval=this.val;
        let eleId=this.id;
        console.info("eleval:"+eleval);
        console.info("eleId:"+eleId);
        if(eleId=='014_payPeriod_after_2' && 0==eleval ){
            this.flag="";
            this.isDisabled=true;
        }
    }


    getValues() {
        if (Object.keys(this.inputVal)) {
            for (let property in this.inputVal) {
                if (this.inputVal.hasOwnProperty(property)) {
                    this[property] = this.inputVal[property];
                }
            }
        }
    }

    keyupHandle() {
        // 禁输负数
        if (this.val < 0) {
            this.val = null;
        }

        let output = {
            val: this.val,
            desc: '',
            key: this.key,
            type: this.type
        }
        this.output.next(output);
    }

    callServe() {
        if (this.isNeedCallServe) {
            let output = {
                val: this.val,
                desc: '',
                key: this.key,
                type: this.type,
                isCallServe: true
            }
            this.output.next(output);
        }
    }
}